import { isActionType, Action, Reducer, ActionCreatorGeneric } from 'redux-typed';
import { assign } from 'lodash';

import { initializeDev } from './sppnpshim';

import * as User from './modules/user';



class CSPRedux { 
    constructor() { 
    };

    public initializeDev = initializeDev;

    public userActions: any = User.actionsUser;

    public mutateInitialState(initialState: any): any { 
        return assign({}, initialState,
            User.initialState
        );
    };
    
    /*
    interface IMutatorReducer {
        (reducer: Reducer<any>): (state: any, action: Action) => any;
    }
    */
    public mutateReducer(reducer: Reducer<any>): ((state: any, action: Action) => any) {
        return (state: any, action: Action) => {
            if (User.checkActionType(action)) {
                return assign({}, User.reducerUsers(state, action));
            }
            return reducer(state, action);
        };
    }
}

export const SPRedux = new CSPRedux();


